@tailwind base;
@tailwind components;
@tailwind utilities;



@layer components {
    body{
        @apply h-[100vh]
    }
    .social-medias li a svg{
        @apply hover:fill-black
    }
    .ast1{
        animation: ast1 3s infinite linear alternate;
    }
    .ast2{
        animation: ast2 3s infinite linear alternate;
    }
    @keyframes ast1 {
        0% {
            transform:translate(-20px,-30px)
        }
    
        to {
            transform:translate(50px,50px)
        }
    
    }
    @keyframes ast2 {
        0% {
            
            transform:translate(0px,50px)
        }
    
        to {
            transform:translate(100px,-100px)
        }
    }
    
}

.slick-prev,.slick-next{
    z-index: 10 !important;
   
   
}
.slick-prev{
    left: 10px !important;
}
.slick-next{
    right: 10px !important;
}
.slick-slide{
    display: grid !important;
    place-items: center !important;
}
